*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}

body{
    height: 100%;
    background: #131313;
    width: 100%;
}
.container{
    padding: 2rem;
    position: absolute;
    top: 50%;
    left: 50%;
    background: #fff;
    overflow-x: hidden;
    overflow-y: auto;
    height: 85vh;
    width: 75vw;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    scroll-behavior: smooth;
    -webkit-box-shadow: 5px 5px rgba(0, 98, 90, 0.4),
    10px 10px rgba(0, 98, 90, 0.3),
    15px 15px rgba(0, 98, 90, 0.2),
    20px 20px rgba(0, 98, 90, 0.1),
    25px 25px rgba(0, 98, 90, 0.05);
            box-shadow: 5px 5px rgba(0, 98, 90, 0.4),
    10px 10px rgba(0, 98, 90, 0.3),
    15px 15px rgba(0, 98, 90, 0.2),
    20px 20px rgba(0, 98, 90, 0.1),
    25px 25px rgba(0, 98, 90, 0.05);
}
h1{
    padding: .1rem 0 .3rem 1rem ;
    color: #444;
    font-family: sans-serif;
    font-weight: 100;
}

textarea{
    outline: none;
    border: none;
    padding: .8rem 0 0 0;
    height: 65%;
    width: 80%;
    overflow: hidden;
}

.same-div{
    padding: 1rem;
}
span{
    color: #aaa;
}
input{
    padding: .3rem 0rem .8rem 0;
    outline: none;
    border: none;
    height: 80%;
    width: 80%;
    border-bottom: 1px solid #aaa;
}
.contact-form{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.btn{
    display: inline;
    margin: 0 0 .1rem 1.8rem; 
    padding: .6rem .8rem;
    border: none;
    border-radius: 45%;
    cursor: pointer;
    font-size: 1rem;
    outline: none;
    background: #ff5349;
    color: #111;
    font-weight: bold;
}

.btn:hover{
    background: #111;
    color: #ff5349;
}


@media only screen and (min-width:250px){
    .container{
        width: 85%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        margin-top: 1rem;
        padding: .8rem 0 .2rem 0;
    }
    input{
        width: 78%;
        margin-bottom: 1rem;
    }
    .mes-input{
        padding-bottom: 2.8rem;
    }
    h1{
        font-size: 1.6rem;
        font-weight: bold;
    }
    .contact-form{
        grid-template-columns: 1fr;
    }
}
@media only screen and (min-width:400px){
    .contact-form, h1{
        margin-left: 1rem;
    }
    input{
        font-size: 1rem;
    }
}


@media only screen and (min-width:585px){
    .container{
        width: 88vw;
        height: 90vh;
        overflow-x: hidden;
        overflow-y: auto;
        margin-top: .6rem;
        padding: 1.5rem 0 0 0;
    }
    input{
        width: 75%;
        margin-bottom: 1rem;
        font-size: 1rem;
    }
    textarea{
        font-size: 1rem;
    }
    .mes-input{
        margin-bottom: 2rem;
    }
    h1{
        font-size: 1.8rem;
        font-weight: 500;
    }
    .contact-form{
        grid-template-columns: 1fr 1fr;
    }
    
}

@media only screen and (min-width:700px){
    .container{
        width: 78%;
        height: 92vh;
        overflow-x: hidden;
        overflow-y: auto;
        margin-top: .6rem;
        padding: 1.5rem 0 0rem .5rem;
        -webkit-box-shadow: 5px 5px rgba(0, 98, 90, 0.4),
        10px 10px rgba(0, 98, 90, 0.3),
        15px 15px rgba(0, 98, 90, 0.2),
        20px 20px rgba(0, 98, 90, 0.1),
        25px 25px rgba(0, 98, 90, 0.05);
                box-shadow: 5px 5px rgba(0, 98, 90, 0.4),
        10px 10px rgba(0, 98, 90, 0.3),
        15px 15px rgba(0, 98, 90, 0.2),
        20px 20px rgba(0, 98, 90, 0.1),
        25px 25px rgba(0, 98, 90, 0.05);
    }
    input{
        width: 82%;
        margin-bottom: 2rem;
        font-size: 1.2rem;
    }
    textarea{
        font-size: 1.2rem;
    }
    h1{
        font-size: 2rem;
        font-weight: 500;
    }
    .contact-form{
        grid-template-columns: 1fr 1fr;
    }
    
}


